import React, { Component } from 'react';
import fbhov from './fbhov.png';
import fb from './fb.png';
import './imghov.css';
class Imghov extends Component {

        render(
        ) {
          return (
            <div class="image-wrapper">
              <img src={fb} class="image" alt="normal" />
              <img src={fbhov} class="image-hover" alt="hover" />
            </div>
          )
        }
      };

export default Imghov



