import React from 'react';
import './projectblock.css';
import fot1 from '../projectimg/2.jpg'

const Projectimg = [
    fot1,
]

export default function Block(
        ProjectPic
    )
    {
        const checkProjectPic = Projectimg.includes(ProjectPic) ? ProjectPic : Projectimg[0]

        return(
            <div className={`block`}

            >
                <div className={'project-display'}
                style={{backgroundImage: `url(${checkProjectPic})`}}></div>

                <div className='description'>
                    
                </div>
            </div>
    )
}